@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensed.woff2") format("woff2"), url("../fonts/robotocondensed.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/robotocondensedbold.woff2") format("woff2"), url("../fonts/robotocondensedbold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/ptmono.woff2") format("woff2"), url("../fonts/ptmono.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  color: #041827;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f8fdff;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #e5e5e5;
  -webkit-text-fill-color: #e5e5e5;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.hidden {
  display: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.accordion__button {
  margin-bottom: 20px;
  padding: 0;
  font-size: 15px;
  line-height: 1.07;
  font-weight: 400;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  text-transform: uppercase;
  text-align: left;
  letter-spacing: 0.8px;
  background-color: transparent;
  border: none;
}
@media (max-width: 767.9px) {
  .accordion__button {
    margin-bottom: 0;
    padding: 21px 25px;
    padding-right: 90px;
    width: 100%;
  }
}

@media (max-width: 767.9px) {
  .is-opened {
    background-image: url("data:image/svg+xml,%3Csvg width='15' height='1' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0h15v1H0V0Z' fill='%23F8FDFF'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: 90% center;
  }
}

@media (max-width: 767.9px) {
  .is-closed {
    background-image: url("data:image/svg+xml,%3Csvg width='15' height='15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.96875 7.03125V0h-.9375v7.03125H0v.9375h7.03125V15h.9375V7.96875H15v-.9375H7.96875Z' fill='%23F8FDFF'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: 90% center;
  }
}

@media (max-width: 767.9px) {
  .accordion__element {
    border-bottom: 1px solid rgba(248, 253, 255, 0.7);
  }
}
.accordion__element--navigation {
  width: 38.5%;
  box-sizing: border-box;
}
@media (max-width: 1023.9px) {
  .accordion__element--navigation {
    width: 100%;
  }
}
.accordion__element--contacts {
  width: 28.5%;
}
@media (max-width: 1023.9px) {
  .accordion__element--contacts {
    width: 100%;
  }
}

@media (max-width: 767.9px) {
  .accordion__element--contacts .accordion__item:first-child {
    margin-bottom: 20px;
  }
}

.accordion__content--navigation {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 6.4%;
  user-select: none;
}
@media (max-width: 1023.9px) {
  .accordion__content--navigation {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
}

.accordion__list {
  margin: 0;
  padding: 0;
}
@media (max-width: 767.9px) {
  .accordion__list {
    margin-top: -5px;
    padding: 0 25px;
  }
}

.accordion__item {
  margin-bottom: 10px;
  list-style: none;
  box-sizing: border-box;
}
@media (max-width: 1023.9px) {
  .accordion__item {
    margin-bottom: 9px;
  }
}
@media (max-width: 767.9px) {
  .accordion__item {
    margin-bottom: 10px;
  }
}

.accordion__link {
  font-size: 16px;
  line-height: 1.35;
  font-family: "PT Mono", "Times New Roman", serif;
  color: #ffffff;
  opacity: 0.7;
}
@media (max-width: 767.9px) {
  .accordion__link {
    font-size: 14px;
    line-height: 1.35;
  }
}
@media (hover: hover) {
  .accordion__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.accordion__link.focus-visible:focus {
  opacity: 0.8;
}

@media (max-width: 767.9px) {
  .accordion__element.is-active .accordion__content {
    min-height: 150px;
  }
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

