.button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.2s;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.14;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.button--primary {
  font-size: 18px;
  line-height: 1.17;
  color: #f2f5f6;
  background-color: #00b2ff;
}
@media (hover: hover) {
  .button--primary:hover:not(.focus-visible) {
    background-color: #009adc;
  }
}
.button--primary.focus-visible:focus {
  background-color: #009adc;
}
.button--light {
  color: #00b2ff;
  background-color: #f8fdff;
  border: 1px solid #00b2ff;
  border-radius: 3px;
}
@media (hover: hover) {
  .button--light:hover:not(.focus-visible) {
    color: #f2f5f6;
    background-color: #00b2ff;
  }
}
.button--light.focus-visible:focus {
  color: #f2f5f6;
  background-color: #00b2ff;
}
.button--dark {
  color: #f2f5f6;
  background-color: #041827;
}
@media (hover: hover) {
  .button--dark:hover:not(.focus-visible) {
    color: #ffffff;
    background-color: transparent;
    box-shadow: 0 0 0 1px #ffffff inset;
  }
}
.button--dark.focus-visible:focus {
  color: #ffffff;
  background-color: transparent;
  box-shadow: 0 0 0 1px #ffffff inset;
}
.button:disabled {
  opacity: 0.5;
  pointer-events: none;
}

